@import url(https://fonts.googleapis.com/css?family=Merienda+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  width: 100%;
  height: 100%;
}
.title {
  text-align: center;
  font-size: 3em;
  padding: 1em 0em;
  font-weight: lighter;
}
.roles {
  display: none;
}
.fas{
  margin-left: 15px;
}
.mobile-roles {
  width: 100%;
  height: 3em;
  display: flex;
  flex-flow: row nowrap;
  border: 1px solid white;
}
.button-role {
  height: 100%;
  flex: 0 0 50%;
  border: 1px solid white;
  text-transform: capitalize;
}
.hidden-roles-show {
  display: block;
}
.hidden-roles-hide {
  display: none;
}

.hidden-roles {
  position: absolute;
  right: 0;
  width: 50%;
  height: 15em;
  background: white;
  display: flex;
  flex-flow: row wrap;
  border: 1px solid white;
}

.hidden-button-role {
  flex: 0 0 100%;
  border: 1px solid white;
}

.champions {
  width: 100%;
  height: 100%;
  margin-top: 2em;
  display: flex;
  flex-flow: row wrap;
}
.champions-box {
  flex: 0 0 93%;
  background: white;
  margin: 3.5%;
  height: 25em;
  border-top-left-radius: 0.5em;
  border-top-right-radius: 0.5em;
  border: 3px solid orange;
  text-decoration: none;
  display: flex;
  flex-flow: column wrap;
}

.champions-image {
  width: 100%;
  height: 85%;
  border-top-left-radius: 0.3em;
  border-top-right-radius: 0.3em;
  object-fit: cover;
}
.champions-name {
  width: 100%;
  height: 15%;
  text-align: center;
  line-height: 2em;
  font-size: 2em;
  font-family: 'Merienda One';
  overflow: hidden;
  white-space: nowrap;
  color: white;
  background: orange;
}

@media only screen and (min-width: 768px) {
  .home {
    width: 100%;
    height: 100%;
  }
  
  .title {
    text-align: center;
    margin: 0.5em;
    font-size: 5em;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    font-weight: lighter;
  }
  .mobile-roles {
    display: none;
  }
  .roles {
    width: 100%;
    height: 4em;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
  }
  .button-role {
    height: 100%;
    font-size: 1.2em;
    color: grey;
    flex: 0 0 16%;
    border: 1px solid white;
    cursor: pointer;
  }
  .button-role:hover {
    border-bottom: 2px solid black;
    color: rgb(231, 123, 50);
  }
  
  .champions {
    width: 100%;
    height: 100%;
    margin-top: 2em;
    display: flex;
    flex-flow: row wrap;
  }
  .champions-box {
    flex: 0 0 43%;
    background: white;
    margin: 3.5%;
    height: 25em;
    border-top-left-radius: 0.5em;
    border-top-right-radius: 0.5em;
    border: 3px solid grey;
    text-decoration: none;
    display: flex;
    flex-flow: column wrap;
  }
  
  .champions-box:hover {
    border: 3px solid orange;
    cursor: pointer;
  }
  .champions-box:hover > .champions-name {
    color: white;
    background: orange;
  }
  .champions-image {
    width: 100%;
    height: 85%;
    border-top-left-radius: 0.3em;
    border-top-right-radius: 0.3em;
    object-fit: cover;
  }
  .champions-name {
    width: 100%;
    height: 15%;
    background: -webkit-gradient(linear, left top, right top, from(rgb(194, 194, 194)), to(grey));
    background: linear-gradient(to right, rgb(194, 194, 194), grey);
    text-align: center;
    line-height: 2em;
    font-size: 2em;
    color: black;
    font-family: 'Merienda One';
    overflow: hidden;
    white-space: nowrap;
  }
}


