.backHome {
  top: 0;
  position: fixed;
  background: none;
  border: 0;
  font-size: 1.5em;
  cursor: pointer;
  padding: 0.5em 0.5em;
  text-decoration: none;
  color: grey;
}
.backHome:hover {
  background-color: hsla(90, 1%, 34%, 1);
  color: white;
}
.details {
  width: 100%;
  height: 100%;
}
.details-box {
  width: 100%;
  height: 30em;
}
.details-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.details-section {
  position: absolute;
  margin-top: -11em;
  width: 100%;
  min-height: 100%;
  background-color: hsla(90, 1%, 34%, 0.8);
}
.details-title {
  color: white;
  text-align: center;
  font-size: 5em;
}
.details-sub-title {
  color: white;
  text-align: center;
  font-size: 2em;
  margin-top: -.5em;
}
.details-roles {
  width: 100%;
  height: 3em;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  margin: 0.5em 0em 0em 0em;
}
.details-role {
  flex: 0 0 30%;
  height: 100%;
  background: rgb(161, 161, 161);
  margin: 0em 0.5em;
  text-align: center;
  line-height: 2.8em;
  font-size: 1em;
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
  text-transform: capitalize;
}

.bio-title {
  text-align: center;
  padding: 0.5em 0;
  color: white;
}
.bio-box {
  width: 100%;
  border: 3px dashed peru;
  margin-bottom: 1em;
}
.bio-p {
  padding: 1em;
  text-align: justify;
  color: white;
}
.details-skills {
  width: 90%;
  height: 5em;
  margin: 0 auto;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}
.details-skills-info {
  width: 90%;
  margin: 1em auto;
  border: 1px solid white;
}
.details-skill {
  flex: 0 0 20%;
  height: 90%;
  margin: 0.5em 0em;
  border: 2px solid hsl(180, 1%, 32%);
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.details-skill-select {
  border: 2px solid orange;
  cursor: pointer;
  margin: 0em 1rem;
}
.details-skill:hover {
  border: 2px solid orange;
  cursor: pointer;
  margin: 0em 1rem;
}
.details-skills-passive {
  border: 2px solid orange;
  cursor: pointer;
  margin: 0em 1rem;
}
.details-skills-q {
  border: 2px solid orange;
  cursor: pointer;
  margin: 0em 1rem;
}
.details-skills-w {
  border: 2px solid orange;
  cursor: pointer;
  margin: 0em 1rem;
}
.details-skills-e {
  border: 2px solid orange;
  cursor: pointer;
  margin: 0em 1rem;
}
.details-skills-r {
  border: 2px solid orange;
  cursor: pointer;
  margin: 0em 1rem;
}
.details-skills-info-p {
  padding: 1em;
  color: white;
  font-size: 1em;
}
.details-skills-info-h1 {
  color:  orange;
  padding: 1em 1em 0em 0.8em;
}  

